let hex16Table=[];
let hexTextTable=[];

let downloadBlob = function(data, fileName, mimeType) {
    var blob, url;
    blob = new Blob([data], {
        type: mimeType
    });
    url = window.URL.createObjectURL(blob);
    downloadURL(url, fileName);
    setTimeout(function() {
        return window.URL.revokeObjectURL(url);
    }, 1000);
};

let downloadURL = function(data, fileName) {
    var a;
    a = document.createElement('a');
    a.href = data;
    a.download = fileName;
    document.body.appendChild(a);
    a.style = 'display: none';
    a.click();
    a.remove();
};

/**
 *
 */
export class ZenData {
    /** @type {DataView}*/
    data=null;
    file=null;
    start=null;
    length=null;
    modified=false
    uid=null;
    children=null;
    viewHexStart=null;
    viewHexLength=null;
    displayChildIndex=false;
    componentName="ZenDataComponent";
    showHex=true;
    bigHex=false;
    displayInformation=null;
    parameterViewBuffered=null;
    groupName=null;
    block=null;
    showChildObjects=true;
    static dataLayout=null;

    constructor(file,start,length) {
        this.file = file;
        if (this.file!=null)
            this.data = file.data;
        this.start = start;
        this.length = length;
        this.viewHexStart = 0;
        this.viewHexLength = this.length = length;
        this.uid=Math.floor(Math.random()*1e10)+"-"+Math.floor(Math.random()*1e10);
        this.bigHex = this.length>500;
        this.showHex = !this.bigHex;
        this.createDisplayInformation();
    }

    static setDataLayout(layout) {
        ZenData.dataLayout = layout;
    }

    createDisplayInformation() {

    }

    readString(offset,length) {
        let arr = new Uint8Array(this.data.buffer, this.start+offset, length);
        return String.fromCharCode.apply(String, arr);
    }

    getUint32(offset) {
        return this.data.getUint32(this.start+offset, true);
    }

    getUint16(offset) {
        return this.data.getUint16(this.start+offset, true);
    }

    getUint8(offset) {
        return this.data.getUint8(this.start+offset );
    }

    getInt32(offset) {
        return this.data.getInt32(this.start+offset, true);
    }

    getInt16(offset) {
        return this.data.getInt16(this.start+offset, true);
    }

    getInt8(offset) {
        return this.data.getInt8(this.start+offset );
    }

    get containerClass() {
        return "";
    }

    get displayChildIndex() {
        return false;
    }

    hex() {
        if (this.viewHexStart==null)
            return null;
        if (this.hexBuffered)
            return this.hexBuffered;
        let startTime=window.performance.now();
        let len = this.viewHexLength;
        let lines=["<div><b>&nbsp;&nbsp;abs&nbsp;&nbsp;&nbsp;&nbsp;off&nbsp;&nbsp;00 01 02 03 &nbsp;04 05 06 07 &nbsp;08 09 0A 0B &nbsp;0C 0D 0E 0F | 0123456789ABCDEF</b></div>"];
        let col=0;
        let ptr = this.viewHexStart;
        let line="";
        let txtLine="";
        while(len>0) {
            if (col===0) {
                line = "<div><b>"+("000000" + (ptr+this.start).toString(16)).substr(-6) +" "+ ("000000" + (ptr).toString(16)).substr(-6)+"</b>";
            }
            let c=this.getUint8(ptr);
            line += hex16Table[c];
            txtLine+= hexTextTable[c];

            col++;
            if ( col % 4 ==0 )
                line+="&nbsp;";
            if ( col===16 ) {
                col=0;
                line+="| "+txtLine;
                line+="</div>";
                lines.push(line);
                txtLine=line = "";
            }
            len--;
            ptr++;
        }
        if (line!="")
            lines.push(line+"</div>");
        this.hexBuffered = lines.join("");

        // let endTime=window.performance.now();
        // let timed = Math.round((endTime-startTime)*1000);
        // console.log(`hex: ${this.viewHexLength} bytes in ${ timed }us = ${timed/this.viewHexLength} `);
        return this.hexBuffered;
    }

    setGroupName(name) {
        if (ZenData.dataLayout==null)
            return ;
        let group = ZenData.dataLayout.groups[name];
        if (group==undefined)
            return console.log(`cannot find group ${name}`);
        console.log(`Set Group ${name}`)
        console.log(group);

        if ( group.totalByteLength>this.length )
            return console.log(`Trying to set group to ${name} but length of group is ${group.totalByteLength}, larger than data ${this.length}`);
        this.children=[];
        for ( const groupItem of group.blocks ) {
            console.log(groupItem);
            if ( groupItem.count==1 ) {
                const newItem = new ZenData(this.file, this.start+groupItem.byteOffset[0], groupItem.blockByteLength);
                newItem.setBlockName(groupItem.blockName);
                this.children.push(newItem);
            } else {
                const newItem = new ZenFixedArray(this.file, this.start+groupItem.byteOffset[0], groupItem.totalByteLength,ZenData, groupItem.count,groupItem.blockByteLength);
                newItem.setBlockName(groupItem.blockName);
                newItem.showChildObjects=false;
                for(let i=0;i<groupItem.count;i++)
                    newItem.children[i].setBlockName(groupItem.blockName);
                this.children.push(newItem);
            }

        }

        this.groupName = name;
    }

    setBlockName(name) {
        if (ZenData.dataLayout==null)
            return ;

        let block = ZenData.dataLayout.blocks[name];
        if (block==undefined)
            return console.log(`cannot find block ${name}`);
        console.log(`set block ${name}`);
        console.log(block);
        this.block = block;
        this.showHex=false;
    }

    hasParameterView() {
        return this.block!=null;
    }

    getParameterValues() {
        if ( this.block == null )
            return null;

        let rv = new Array();
        for ( let param of this.block.parameters ) {
            let val=null;

            // fix NAME
            let nameMatch = param.id.match(/NAME_(\d+)/);
            if ( nameMatch!==null ) {
                if (nameMatch[1]!='1')
                    continue;
                rv.push(this.readString(param.byteOffset,16).trimRight());
                continue;
            }

            switch ( param.lengthBytes ) {
                case 1:
                    val = this.getInt8(param.byteOffset);
                    break;
                case 2:
                    val = this.getInt16(param.byteOffset);
                    break;
                case 4:
                    val = this.getInt32(param.byteOffset);
                    break;
            }

            if ( val!=null) {
                if ( param.values !== null) {
                    val = param.values[val]===undefined ? 'invalid value' : param.values[val];
                } else if ( param.displayRange!==undefined ) {
                    val = ( val - param.dataRange[ 0 ] ) * ( param.displayRange[ 1 ] - param.displayRange[ 0 ] ) / ( param.dataRange[ 1 ] - param.dataRange[ 0 ] ) + param.displayRange[ 0 ];
                    let multiplicator = Math.pow(10, 1);
                    let n = parseFloat((val * multiplicator).toFixed(11));
                    val = Math.round(n) / multiplicator;

                    console.log(val);
                }
                rv.push(String(val));
            } else
                rv.push("xxx");
        }
        return rv;
    }

    // override for arrays
    getAllParameterValues() {
        return[ this.getParameterValues() ];
    }

    parameterView() {
        if ( this.block == null )
            return null;

        if ( this.parameterViewBuffered != null )
            return this.parameterViewBuffered;

        let paramData = this.getAllParameterValues();
        let paramDataCount = paramData.length;

        let out=new Array();
        out.push("<table class='table table-sm table-striped table-bordered'>");
        let row=0;
        for ( let param of this.block.parameters ) {
            // fix NAME
            let nameMatch = param.id.match(/NAME_(\d+)/);
            if ( nameMatch!==null ) {
                if (nameMatch[1]!=='1')
                    continue;
            }

            let desc = param.displayMeasurement!=null ? `${param.description} (${param.displayMeasurement})` : param.description;

            out.push('<tr>');
            out.push(`<td><b>${htmlEncode(desc)}</b></td>`);
            for (let value of paramData ) {
                out.push(`<td>${value[row]}</td>`);
            }
            out.push('</tr>');
            row++;
        }
        out.push("</table");
        this.parameterViewBuffered = out.join('');

        return this.parameterViewBuffered;
    }

    saveFile() {
        let dv = new Uint8Array(this.data.buffer, this.start, this.length);
        let blob = new Blob(dv, {type: "octet-stream"});
        let fname = `${this.file.name}_${this.start}_${this.length}`;
        if (this.name!==undefined)
            fname = this.name;
        if ( this.getDownloadFilename !== undefined )
            fname = this.getDownloadFilename();

        fname += ".bin";
        downloadBlob(dv, fname, 'application/octet-stream');
    }

    toggleShowHex() {
        this.showHex = ! this.showHex;
    }

    toggleShowChildObjects() {
        this.showChildObjects = ! this.showChildObjects;
    }
}



