// eslint-disable-next-line
class ZenChunk extends ZenData {
    type=null;

    constructor(type,data,start, length) {
        super(data, start, length);
        this.type = type;
        this.displayChildIndex=true;
        this.componentName="ZenChunkComponent";
    }

    static createZenChunk(type,data,start,length) {
        if (ZenChunkTypes[type]!==undefined) {
            return new ZenChunkTypes[type](type, data, start, length)
        }
        return new ZenChunk(type, data, start, length);
    }


    read() {}
}

/**
 *
 */
class ZenChunkWithArray extends ZenChunk {
    constructor(type,data,start,length, childType, parentType) {
        super(type, data, start, length);
        if ( childType===undefined )
            childType = ZenData;
        this.viewHexStart=null;
        this.read( childType,parentType );
    }

    read(childType,parentType) {
        this.children = new Array(1);
        if ( parentType!==undefined )
            this.children[0] = new parentType(this.file, this.start,this.length,childType);
        else
            this.children[0] = new ZenArray(this.file, this.start,this.length,childType);
    }

}

/**
 *
 */
class ZenPATaZCOR extends ZenChunkWithArray {
    constructor(type,file,start,length) {
        super(type, file, start, length,ZenTonePATObject,ZenToneArray);
        this.componentName="ZenPATaZCORComponent";
    }
}

class ZenMDLaZCOR extends ZenChunkWithArray {
    constructor(type,file,start,length) {
        super(type, file, start, length,ZenToneMDLObject,ZenToneArray);
        this.componentName="ZenMDLaZCORComponent";
    }
}

/**
 *
 */
const ZenToneType = Object.freeze({
    unknown:"unknown",
    pat:"ZenCore",
    mdl:"Model",
});

/**
 *
 */
class ZenDataWithName extends ZenData {
    constructor(file,start,end) {
        super(file,start,end);
        this.name = this.readString(0,16).trimRight();
    }
}

/**
 *
 */
class ZenToneObject extends ZenDataWithName {
    type=ZenToneType.unknown;
    checksum=null;
    location="";

    constructor(file,start,end) {
        super(file,start,end);
        this.showHex=false;
        this.displayChildIndex=true;
    }

    createDisplayInformation() {
        let di={};
        di['Name']=this.name;
        di['Type']=this.type;
        di['Location']=this.location;
        di['Checksum']=this.checksum;
        this.displayInformation = di;
    }

    getDownloadFilename() {
        return this.name +"_"+( this.type==ZenToneType.mdl ? "MDL" : "PAT" );
    }
}

