/**
 *
 *
 */
const ZenFileType = Object.freeze({
    unknown:0,
    svz:1,
    mpj:2,
    svd:3
});

class Tone {
    name=null;
    type=ZenToneType.unknown;
    checksum=null;
    uid=Math.floor(Math.random()*1e10)+"-"+Math.floor(Math.random()*1e10);
}

/**
 *
 */
export class ZenFile extends ZenData {
    name="";
    type=ZenFileType.unknown;
    fileLength=0;
    tones=[];

    constructor(name,data) {
        super(null,0, data.byteLength);
        this.data = data;
        this.file=this;
        this.name=name;
        this.fileLength=data.byteLength
        this.componentName = "ZenFileComponent";
    }

    read() {
        if ( this.data.getUint32(0)==0x53565A61 ) {
            this.type=ZenFileType.svz;
            this.readChunks(this.data.getUint8(4));
        } else if ( this.data.getUint32(2)==0x50524A35 ) {
            this.type=ZenFileType.mpj;
            let numChunks = (this.getUint16(0)+2-16) /16;
            this.readChunks(numChunks);
        }  else if ( this.data.getUint32(2)==0x53564435 ) {
            this.type = ZenFileType.svd;
            let numChunks = (this.getUint16(0) + 2 - 16) / 16;
            this.readChunks(numChunks);
        }
        else
            this.viewHexLength = Math.min(this.length, 1024);
    }

    readChunks( numChunks ) {
        this.children = new Array( numChunks );
        for ( let i=0; i<numChunks; i++ ) {
            let offset = 16*(i+1);
            let type = this.readString(offset,8);
            let length = this.getUint32(offset+12);
            let start = this.getUint32(offset+8);
            this.children[i] = ZenChunk.createZenChunk(type, this.file,start, length);
        }
        this.viewHexStart=0;
        this.viewHexLength=16+numChunks*16;
        this.showHex = true;

    }

    get typeDescription() {
        switch ( this.type ) {
            case ZenFileType.svz:
                return "SVZ";
            case ZenFileType.mpj:
                return "MPJ"
        }
        return "Unknown";
    }
}

let ZenChunkTypes = {
    "PATaZCOR":ZenPATaZCOR,
    "MDLaZCOR":ZenMDLaZCOR,
    "DIFaZCOR":ZenChunkWithArray,
    "PRJaMC77":ZenPRJChunk,
    "PRFaKY19":ZenChunkWithArray,
    "PATaKY19":ZenPATaZCOR,
    "SNAaKY19T":ZenChunkWithArray,
    "SYSaKY19":ZenChunkWithArray,
    "DIFaKY19":ZenChunkWithArray
}

// build hex/text table
for ( let i=0; i<256;i++) {
    hex16Table.push(" " + ("00" + (i).toString(16)).substr(-2).toUpperCase());
    if (i<32)
        hexTextTable.push(".");
    else
        hexTextTable.push(String.fromCharCode(i));
}
