<template>
    <div @drop.stop.prevent="dropFile" @dragover.prevent="">
        <div class="container">
            <h1>ZenInspector build 8 <small style="font-size: .475em">by <a href="https://splunge.foo">Splunge.foo</a></small>
            </h1>
            <div class="container pb-4">
                <div><b>An app for poking into Roland ZenCore sound files used in Zenology, Fantom and Fantom 0, MC-101,
                    MC-707, Jupiter X/Xm, Juno X etc. Hopefully this will become an editor/librarian.</b></div>
                <div>Drag/drop an svz/mpj/svd onto this page to read.</div>
                <div class="mt-2 mb-2">
                <div>Eyeball<i class='ms-1 me-2 bi-eye' style="font-size: 1rem;"></i>shows/hides children.</div>
                <div><b>0x</b> shows/hides hex values.</div>
                <div>Download <i class='ms-1 me-2 bi-download' style="font-size: 1rem;"></i>individual parts for further inspection in your favorite hex editor.</div>
                </div>
                <div>Anything with samples - you're going to have a slow time if you try and look at the hex in there.</div>
                <div>If you are interested in helping out discover more information or are after data come and join us
                    at the <a href="https://discord.gg/Kf7gEDFzfV" target="_blank"> discord server</a>.
                </div>
            </div>
            <ZenDataComponent :item="file"></ZenDataComponent>
        </div>
    </div>
</template>

