import {ZenData, ZenFile} from "@/components/ZenCore";

import jupiter from "@/components/jupiter";

export default {
  name: 'App',
    data() {
      return {
        file: null,
          lotteryNumbers:""
        }
    },
    methods: {
      dropFile(event) {
          const file = event.dataTransfer.files[0];
          const reader = new FileReader();
          reader.onload = e => {
              this.readFile(e,file);
          };
          reader.readAsArrayBuffer(file);
          return false;
      },
        readFile(reader,file) {
            let dv = new DataView( reader.currentTarget.result );
            this.file = new ZenFile(file.name,dv);
            this.file.read();
        },
        revealOrgan() {
          let a=0x10, b=0x30;
          this.lotteryNumbers="theorgangrinder";
          this.lotteryNumbers+=String.fromCharCode(a+b );
          this.lotteryNumbers+="unrelated-domainname.com";
        },
        saveFile(item) {

        }

    },
  components: {
  },
    created() {
        ZenData.setDataLayout(jupiter);
        let dv = new DataView( initData.buffer );
        this.file = new ZenFile("JP-8K S-Saw Pad",dv);
        this.file.read();
        console.log(this.file);
    },
}

